<script>
import TheHeader from "./../../components/TheHeader";
import { historystyle,savestyle,setstyle } from "./../../api/index";
export default {
  components: {
    TheHeader,
  },
  data() {
    return {
      id:'',
      formatSets:'',
      historicalOptions: [
        {
          value: "选项1",
          label: "黄金糕",
        },
        {
          value: "选项2",
          label: "双皮奶",
        },
        {
          value: "选项3",
          label: "蚵仔煎",
        },
        {
          value: "选项4",
          label: "龙须面",
        },
        {
          value: "选项5",
          label: "北京烤鸭",
        },
      ],
      historical: "",
      styleName: "",
      dataForm: {
        js: "", //期刊
        bs: "", //书籍
        cs: "", //会议
        ts: "", //论文
        ws: "", //网页
        aen: 'False', //作者全英 False表示保留中英 True 为姓名全英
        aposition: 'False', //姓名位置 False 为姓前名后 True 为名前姓后
        afenge: "", //姓名分隔
        asuoxie: "", //名缩写
        azuozhejian: "", //作者之间
        lastazuozhejian: "", //最后作者之间间隔
        anum: "", //作者数目
        intext: 'False', //intext合并 True 代表单独导出 False 代表 合并导出
        volhei: 'False', //加粗（卷号）False 正常  True 加粗
        volxie: 'False', //正斜体（卷号）False 正常 True 斜体
        bten: 'False', //标题全英 True 代表 保留中英 False 代表 全英标题
        btcap: 'False', //标题大写 False 代表词首大写 True 代表 句首大写
        numhei: 'False', //加粗 期号 False 正常 True 加粗
        numxie: 'False', //正斜体 期号 False 正常 True 斜体
        qken: 'False', //期刊全英 False 代表保留中英 True 代表全英期刊
        abvqk: 'False', //期刊缩写 False  显示全名 True 缩写
        journalABinC: 'False', //期刊名词大写 False 词首大写 True 词首大写
        pagemark: "", //连接符
        pagestart: 'False', //起始码 False 起始结束 True 开始
      },
      tableData:[]
    };
  },
  created(){
    this.id = this.$route.query.id;
    this.formatSets=JSON.parse(window.localStorage.getItem("formatSets"));
    // console.log( this.id,this.formatSets);
    this.historicalOptions=[]
    this.formatSets.formatlist.forEach(item=>{
      this.historicalOptions.push({value: item.formatname,
          label: item.formatname})
    })
    this.analysis(this.formatSets)
  },
  methods:{
    setHistorystyle(){
        historystyle(
            this.id,this.historical
        )
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            this.analysis(res)
            this.$message({
              message: '设置成功',
              type: 'success'
            });
            window.localStorage.setItem("formatSets", JSON.stringify(res));
          } else {
            this.notify(res.msg ? res.msg : "请求失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);       
        });
    },
    toSave(){
        savestyle(
            // style,task_id,custom_id
            {
              style:this.styleName,
              task_id:this.id,
              custom_id:this.formatSets.data.custom_id
            }
        )
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            this.$message({
              message: '设置成功',
              type: 'success'
            });
            window.localStorage.setItem("formatSets", JSON.stringify(res));
            this.analysis(res)
          } else {
            this.notify(res.msg ? res.msg : "请求失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);       
        });
    },
    analysis(datas){
      // this.dataForm=datas.format_dict
      this.dataForm={
        js: datas.format_dict.js, //期刊
        bs: datas.format_dict.bs, //书籍
        cs: datas.format_dict.cs, //会议
        ts: datas.format_dict.ts, //论文
        ws: datas.format_dict.ws, //网页
        aen: this.BooleanJude(datas.format_dict.aen), //作者全英 False表示保留中英 True 为姓名全英
        aposition: this.BooleanJude(datas.format_dict.aposition), //姓名位置 False 为姓前名后 True 为名前姓后
        afenge: datas.format_dict.afenge, //姓名分隔
        asuoxie: datas.format_dict.asuoxie, //名缩写
        azuozhejian: datas.format_dict.azuozhejian, //作者之间
        lastazuozhejian: datas.format_dict.lastazuozhejian, //最后作者之间间隔
        anum: datas.format_dict.anum, //作者数目
        intext: this.BooleanJude(datas.format_dict.intext), //intext合并 True 代表单独导出 False 代表 合并导出
        volhei: this.BooleanJude(datas.format_dict.volhei), //加粗（卷号）False 正常  True 加粗
        volxie: this.BooleanJude(datas.format_dict.volxie), //正斜体（卷号）False 正常 True 斜体
        bten: this.BooleanJude(datas.format_dict.bten), //标题全英 True 代表 保留中英 False 代表 全英标题
        btcap: this.BooleanJude(datas.format_dict.btcap), //标题大写 False 代表词首大写 True 代表 句首大写
        numhei: this.BooleanJude(datas.format_dict.numhei), //加粗 期号 False 正常 True 加粗
        numxie: this.BooleanJude(datas.format_dict.numxie), //正斜体 期号 False 正常 True 斜体
        qken: this.BooleanJude(datas.format_dict.qken), //期刊全英 False 代表保留中英 True 代表全英期刊
        abvqk: this.BooleanJude(datas.format_dict.abvqk), //期刊缩写 False  显示全名 True 缩写
        journalABinC: this.BooleanJude(datas.format_dict.journalABinC), //期刊名词大写 False 词首大写 True 词首大写
        pagemark: datas.format_dict.pagemark, //连接符
        pagestart: this.BooleanJude(datas.format_dict.pagestart), //起始码 False 起始结束 True 开始
		custom_id: datas.custom_id,
      }
      this.tableData=datas.referencelist
    },
    BooleanJude(val){
      let  bool = 'False';
      if(val=='true'||val==true){
        bool='True'
      }else if(val=='false'||val==false){
        bool='False'
      }
      return bool
    },
    toset(){
        setstyle({
            js: this.dataForm.js, //期刊
            bs: this.dataForm.bs, //书籍
            cs: this.dataForm.cs, //会议
            ts: this.dataForm.ts, //论文
            ws: this.dataForm.ws, //网页
            aen: this.dataForm.aen, //作者全英 False表示保留中英 True 为姓名全英
            aposition: this.dataForm.aposition, //姓名位置 False 为姓前名后 True 为名前姓后
            afenge: this.dataForm.afenge, //姓名分隔
            asuoxie: this.dataForm.asuoxie, //名缩写
            azuozhejian: this.dataForm.azuozhejian, //作者之间
            lastazuozhejian: this.dataForm.lastazuozhejian, //最后作者之间间隔
            anum: this.dataForm.anum, //作者数目
            intext: this.dataForm.intext, //intext合并 True 代表单独导出 False 代表 合并导出
            volhei: this.dataForm.volhei, //加粗（卷号）False 正常  True 加粗
            volxie: this.dataForm.volxie, //正斜体（卷号）False 正常 True 斜体
            bten: this.dataForm.bten, //标题全英 True 代表 保留中英 False 代表 全英标题
            btcap: this.dataForm.btcap, //标题大写 False 代表词首大写 True 代表 句首大写
            numhei: this.dataForm.numhei, //加粗 期号 False 正常 True 加粗
            numxie: this.dataForm.numxie, //正斜体 期号 False 正常 True 斜体
            qken: this.dataForm.qken, //期刊全英 False 代表保留中英 True 代表全英期刊
            abvqk: this.dataForm.abvqk, //期刊缩写 False  显示全名 True 缩写
            journalABinC: this.dataForm.journalABinC, //期刊名词大写 False 词首大写 True 词首大写
            pagemark: this.dataForm.pagemark, //连接符
            pagestart: this.dataForm.pagestart, //起始码 False 起始结束 True 开始
            custom_id:this.formatSets.data.custom_id,
            task_id:this.id,
        })
        .then((res) => {
          console.log(res);
          if (res.code == 200) {
            window.localStorage.setItem("formatSets", JSON.stringify(res));
            this.analysis(res)
            this.$message({
              message: '设置成功',
              type: 'success'
            });
          } else {
            this.notify(res.msg ? res.msg : "请求失败", "error");
          }
        })
        .catch((err) => {
          console.log(err);       
        });
    },
    toDownFile(){
      console.log("下载文档");
    }
  }
};
</script>
